          margin: 0; }
      .mainWrapper #upcoming .allShows {
        right: 8pt;
        bottom: 8pt;
        font-size: 1.2em; } }

input, textarea {
  color: #6c6c6c;
  transition: all 0.2s ease-in-out; }

input:focus, textarea:focus {
  color: #2d2d2d; }

input[type=text], input[type=password], textarea {
  height: 32pt;
  padding: 10px;
  margin: 5px 1px 3px 0px;
  font-size: 1em;
  border-radius: 5px;
  outline: none;
  border: 1px solid #DDDDDD;
  -webkit-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out;
  -moz-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out;
  -ms-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out;
  -o-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out; }

input[type=text]:focus, input[type=password]:focus, textarea:focus, input.inputValid {
  box-shadow: 0 0 5px #74e273;
  border: 1px solid #74e273; }

input.inputError, input.inputError:focus {
  box-shadow: 0 0 5px #e25442;
  border: 1px solid #e25442; }
  input.inputError::before, input.inputError:focus::before {
    content: '!';
    color: #e25442;
    font-size: 2.2em; }

select {
  height: 32pt;
  border-radius: 5px;
  border-color: #dddddd;
  min-height: 40px;
  padding-left: 5px;
  outline: none;
  -webkit-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out;
  -moz-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out;
  -ms-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out;
  -o-transition: box-shadow 0.2s ease-in-out, border 0.2s ease-in-out; }

select:focus, select:active {
  box-shadow: 0 0 5px #74e273;
  border: 1px solid #74e273; }

option:hover {
  box-shadow: 0 0 5px #74e273; }

hr {
  border: none;
  height: 3px;
  width: 100%;
  background: #2d2d2d; }

i {
  cursor: pointer; }
  i > span {
    margin-left: 4pt;
    font-family: "Nunito", "Open Sans", sans-serif; }

.warning {
  color: #d31f25 !important;
  text-align: left;
  pointer-events: none; }

body {
  overflow-x: hidden; }
